<template>
  <div>
    <div class="page-title">
      <h3>Общая статистика отрядов</h3>

      <button class="btn waves-effect waves-light btn-small" @click="refresh">
        <i class="material-icons">refresh</i>
      </button>
    </div>
    <div class="row">
      <p>Выберите период:</p>
      <select style="display: block" v-model="choosed" ref="sq">
        <option v-for="d of period" :key="d.id">{{ d.title }}</option>
      </select>
      <Loader v-if="loading" />
      <v-table v-else :data="n" class="responsive-table highlight">
        <thead slot="head">
          <th>Место</th>
          <th>Отряд</th>
          <v-th sortKey="kills" defaultSort="desc">Убийств</v-th>
          <v-th sortKey="teamkills" defaultSort="desc">Тимкиллов</v-th>
          <v-th sortKey="score" defaultSort="desc">Счет</v-th>
        </thead>
        <tbody slot="body" slot-scope="{ displayData }">
          <tr v-for="(row, index) in displayData" :key="row.id">
            <td>{{ index + 1 }}</td>
            <td>{{ row.prefix }}</td>
            <td>{{ row.kills }}</td>
            <td>{{ row.teamkills }}</td>
            <td>{{ row.score }}</td>
          </tr>
        </tbody>
      </v-table>
    </div>
  </div>
</template>

<script>
export default {
  name: "Squads",
  data: () => ({
    n: [],
    loading: true,
    choosed: "За все время",
    period: [],
    squads: [],
  }),
  async mounted() {
    this.squads.push("[Одиночки]");
    let s = await fetch(`stats.json?t=${Date.now()}`);
    s = await s.json();
    for (let key in s.squadStatistics) {
      this.squads.push(s.squadStatistics[key].prefix);
    }
    let r = await fetch(`rotations_stats.json?t=${Date.now()}`);
    r = await r.json();
    for (let key in r) {
      let tStartDate = r[key].startDate;
      let tEndDate = r[key].endDate;
      r[key].startDate = new Date(r[key].startDate).toLocaleString("ru", {
        year: "numeric",
        month: "short",
        day: "numeric",
      });
      if (r[key].endDate === null) {
        r[key].endDate = " по настоящий момент.";
      } else {
        r[key].endDate = new Date(r[key].endDate).toLocaleString("ru", {
          year: "numeric",
          month: "short",
          day: "numeric",
        });
      }
      this.period.push({
        id: Number(key),
        title: `${r[key].startDate} - ${r[key].endDate}`,
        endDate: tEndDate,
        startDate: tStartDate,
      });
    }
    this.period.push({
      id: r.length,
      title: "За все время",
    });
    this.period.reverse();
    await this.refresh();
  },
  watch: {
    choosed: function () {
      this.refresh();
    },
  },
  methods: {
    async refresh() {
      if (this.choosed == this.period[0].title) {
        this.r();
      } else {
        this.rotationTable();
      }
    },
    async r() {
      this.loading = true;
      this.n = [];
      let r = await fetch(`stats.json?t=${Date.now()}`);
      r = await r.json();
      let id = 1;
      for (let key in r.squadStatistics) {
        r.squadStatistics[key].id = id;
        id++;
        this.n.push(r.squadStatistics[key]);
      }
      this.loading = false;
    },
    async rotationTable() {
      this.loading = true;
      this.n = [];
      let r = await fetch(`rotations_stats.json?t=${Date.now()}`);
      r = await r.json();
      let id = 1;
      let rotationID = this.$refs.sq.selectedIndex;
      for (let key in r) {
        if (r[key].startDate == this.period[rotationID].startDate) {
          for (let key1 in r[key].stats.squad) {
            this.n.push(r[key].stats.squad[key1]);
          }
        }
      }
      this.loading = false;
    },
  },
};
</script>
